.message-header{
    position: fixed;
    width: 100%;
    height: 1.173333rem /* 44/37.5 */;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    font-size: .426667rem /* 16/37.5 */ !important;
    text-align: center;
    box-shadow:0 .053333rem /* 2/37.5 */ .133333rem /* 5/37.5 */ #f5f5f5; 
}
.message-setting {
    /* font-size: .426667rem 16/37.5 !important; */

    height: 1.173333rem /* 44/37.5 */;
    width: 1.6rem /* 60/37.5 */;
    line-height: 1.333333rem /* 50/37.5 */;
    position: fixed;
    float: right;
    text-align: center;
    right: 0;
    /* text-align: right; */
}
.message-text {
    height: 100%;
    /* width: 100%; */
    flex: 1;
    line-height: 1.333333rem /* 50/37.5 */;
}
.message-content {
    /* width: 100%; */
    height: 9.333333rem /* 350/37.5 */;
    /* position: fixed; */
    /* left: 0;
    right: 0; */
    border-bottom: 1px solid #f5f5f5;
    padding: 0 .266667rem /* 10/37.5 */;
    margin-bottom: .266667rem /* 10/37.5 */;
    /* margin-top: 50px; */
    box-sizing: border-box;

}
.message-item {
    width: 100%;
    height: 2.133333rem /* 80/37.5 */;
    padding: .266667rem /* 10/37.5 */ 0;
    display: flex;
    align-items: center ;
    box-sizing: border-box;
}
.message-item-avatar {
    width: 1.333333rem /* 50/37.5 */;
    height: 1.333333rem /* 50/37.5 */;
    margin-right: .266667rem /* 10/37.5 */;
}
.message-item-avatar > img {
    border-radius: .666667rem /* 25/37.5 */;
}
.message-item-info {
    flex: 1;
}
.message-item-info__name {
    font-size: .426667rem /* 16/37.5 */;
}
.message-item-info__content  {
    font-size: .32rem /* 12/37.5 */;
}
.message-block {
    display: block;
    
}

.message-item-time {
    height: .533333rem /* 20/37.5 */;
    font-size: .32rem /* 12/37.5 */;
    margin-right: .266667rem /* 10/37.5 */;
}
.message-recommend-banner {
    width: 100%;
    height: 2.133333rem /* 80/37.5 */;
    padding: .266667rem /* 10/37.5 */;
    background-color: red;
    box-sizing: border-box;
}